import React from 'react';
import {withRouter} from "react-router-dom";

const Banner = () => {
  return (
    <div className="site-blocks-cover overlay aos-init aos-animate"
         style="background-image: url(images/hero_img.jpg); background-position: 50% -25px;" data-aos="fade"
         data-stellar-background-ratio="0.5" id="home-section">
      <div className="container">
        <div className="row align-items-center justify-content-center text-center">
          <div className="col-lg-8">
            <h1 className="text-white aos-init aos-animate" data-aos="fade-up" data-aos-delay="0">I'm Farrukh
              Afaq <br /> Senior Front-end Developer</h1>
          </div>
        </div>
      </div>
    </div>
  );
};

export default withRouter(Banner);